@import '../utils/_mixins';
@import '../utils/_variables';
.header-middle {
	&__list {
		align-items: center;
		color: #fff;
		font-family: @custom-font-family;
		font-size: 18px;
		margin-bottom: 75px;
		margin-top: 29px;	
		text-align: center;
		&-inner {
			div {
				display: inline;
			}	
			span, a {
				display: inline;			
			}
			span {
				opacity: .7;
				transition: opacity .3s ease-in
			}
			a:hover span {
				opacity: 1
			}
			.sep {
				font-style: normal;
				margin: 0 5px 0 5px;
			}
		}
		&_title {
			position: relative;
			padding-left: 42px;
		}
	}
	&__logo {
		height: auto;
		max-width: 30px;
		max-height: 30px;
		position: absolute;
		left: 0;
		top: 50%;
		transform: translate(0,-50%);
		width: auto
	}
	&-project {
		margin-top: -35px;
	}
}

.bread-crumbs {
	list-style: none;
	&__item {
		display: inline-block;
		vertical-align: top;
		&:last-child {
			padding-right: 0;
			.bread-crumbs__link{
				padding-right: 0;
				&:after {
					display: none;
				}
			}
		}
	}
	&__link {
		color: #fff;
		display: inline-block;
		position: relative;
		font-family: @custom-font-family;
		font-size: 20px;
		margin-bottom: 7px;
		padding-right: 30px;
		opacity: 0.7;
		&:hover {
			opacity: 1;
		}
		&:after {
			content: '→';
			display: block;
			margin: 0 5px;
			right: -2px;
			top: 0;
			position: absolute;
		}
	}
}
.money-count span {
	font-family: @base-font-family;
}
.activity-item-count {
	font-size: 80px;
    font-weight: 700;
