		top: 57px;
		position: relative;
		transition: top .2s ease-in;
	}
	.nav-tab[style*="fixed"] {
		//transform: translate(0,148px);
	}
}
.nav-tab {
	list-style: none;
	&__link {
		color: #9b9b9b;
		display: table;
		font-family: @custom-font-family;
		font-size: 16px;
		padding: 0 20px;
		min-height: 51px;
		align-items: center;
		position: relative;
		border-radius: 8px;
		width: 100%;
		&:before {
			content: '';
			bottom: 0;
			display: block;
			height: 1px;
			position: absolute;
			left: 0;
			width: 100%;
			background-image: linear-gradient(to left, rgba(238, 238, 238, 0), #eaeaea 14%, #dbdbdb 88%, rgba(216, 216, 216, 0));
		}
		span {
			display: table-cell;
			vertical-align: middle;
			width: 100%
		}
		&:hover {
			color: @text-color;
		}
		&.active, &.active:hover {
			color: #fff;
			cursor: default;
			background-image: linear-gradient(to right, @color-active, #31c966);
			&:before {
				background-image: none;
			}
		}
	}
}
.tab {
	display: none;
	&.active {
		display: block;
	}
}

