}
.purpose {
	display: flex;
	flex-wrap: wrap;
	margin: 37px -10px 0;
	position: relative;
	.slick-track {
		display: flex;
		flex-wrap: wrap;
	}
	&.slick-initialized .slick-slide {
		display: flex;
	}
	&__item {
		align-items:  center;
		border-radius: 8px;
		border: 1px solid @line;
		display: flex;
		font-family: @custom-font-family;
		height: auto;
		margin: 0 10px 19px;
		padding: 30px 30px 33px 35px;
		width: calc(~"33.3% - 20px");
		transition: box-shadow .3s ease-in;
		&-green {
			border-color: @color2;
			.purpose__count{
				color: @color2;
			}
		}
		&-yellow {
			border-color: @color3;
			.purpose__count{
				color: @color3;
			}
		}
		&-red {
			border-color: @color4;
			.purpose__count{
				color: @color4;
			}
		}
	}
	&__count {
		color: @color2;
		font-size: 114px;
		font-weight: 700;
		flex-shrink: 0;
		transition: transform .3s ease-in;
	}
	&__description {
		color: #4a4a4a;
		font-size: 16px;
		line-height: 1.5;
		padding-left: 29px
	}
}
.purpose-project {
	.purpose__item-green {
