/* modals */

.modal .mCSB_vertical .mCSB_container {
	padding-bottom: 40px
}
.modal-volunteer .mCSB_vertical .mCSB_container {
	padding-bottom: 0
}
.fancybox-active {
	.main-wrapper {
		filter: blur(5px);
	}
}
.modal {
	background: #fff;
	border-radius: 8px;
	display: inline-block !important;
	opacity: 0;
	padding: 50px 0 0;
	overflow: hidden;
	z-index: -1000;
	visibility: hidden;
	position: absolute;
	left: 0;
	top: 0;
	transition: opacity .3s ease;
	max-height: 100%;
	width: 100%;
	&__head {
		padding:0 50px 15px;
	}
	&__body {
		padding: 15px 50px 0;
	}
	&-big {
		max-width: 1000px
	}
	.vertical-scroll > .mCustomScrollBox:not([style*="none"]) {
		margin-right: -50px;
		max-width: calc(~"100% + 50px");
		> .mCSB_container {
			margin-right: 50px
		}
	}
}
.fancybox-container .modal {
	position: relative;
	visibility: visible;
	left: 0;
	top: 0;
	bottom: 0;
	opacity: 1;
	z-index: 100;
}
.vertical-scroll > .mCustomScrollBox[style*="none"] {
	> .mCSB_scrollTools {
		display: none !important
	}
	> .mCSB_container {
		margin-right: 0
	}
}
.mCSB_vertical > .mCSB_scrollTools {
	top: 5px;
	bottom: 5px;
} 
